.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  background: var(--white);
  width: auto;
  padding: 0;
  font-size: 0.875rem;
  border-radius: 0.5rem;
}
.card__name, .card__price, .card__rprice,
.card h1, .card__button,
.card p {
  padding: 0.3125rem 1.25rem 0.3125rem 1.25rem;
}
.card__img1 {
  width: 100%;
  border-top-left-radius: 0.5rem;
}
@media (max-width: 48em) {
  .card__img1 {
    border-top-right-radius: 0.5rem;
  }
}
.card__img2 {
  display: none;
}
.card__name {
  font-size: 0.75rem;
  margin-top: 0.625rem;
  text-transform: uppercase; /* Converts text to uppercase */
  letter-spacing: 0.3125rem; /* Adds 1em space between each character */
  color: var(--dkgrayishblue);
}
.card p {
  margin-top: -0.3125rem;
  margin-bottom: 0.3125rem;
  color: var(--dkgrayishblue);
}
.card__box {
  display: flex;
  align-items: center; /* Align items vertically in the center */
  margin-bottom: -0.625rem;
}
.card__price {
  font-size: xx-large;
  font-family: Fraunces;
  color: var(--dkcyan);
  margin-right: -1.25rem; /* Add margin to create space between the price and rprice */
}
.card__rprice {
  font-size: 0.75rem;
  text-decoration-line: line-through;
  color: var(--dkgrayishblue);
}
.card__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  height: 4vh;
  margin: 1.25rem auto;
  background: var(--dkcyan);
  border-radius: 0.5rem;
  position: relative;
}
.card__button img {
  position: relative;
  bottom: 0.0625rem;
  left: 0.125rem;
  width: 1rem;
  height: 0.9375rem;
}
.card__button h2 {
  position: relative;
  left: 0.625rem;
  top: 0.3125rem;
  font-size: 0.875rem;
  color: var(--white);
}
.card__button:hover {
  cursor: pointer;
  background: var(--verydkblue);
}
@media (min-width: 48em) {
  .card {
    flex-direction: row; /* Changes the flex direction to row for desktop */
    align-items: stretch; /* Aligns items vertically stretch */
    width: 31.25rem;
  }
  .card__img1 {
    display: none;
  }
  .card__img2 {
    height: 15.625rem;
    width: 15.625rem;
    display: block;
    height: auto; /* Keep aspect ratio */
    border-bottom-left-radius: 0.5rem; /* Rounds bottom-left corner */
    border-top-left-radius: 0.5rem; /* Rounds top-left corner */
  }
  .card .content {
    display: flex;
    flex-direction: column;
    justify-content: start;
    padding: 1rem;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Montserrat", sans-serif;
  background: var(--cream);
  height: 100vh;
  padding: 1.25rem 0.625rem 0 0.625rem;
}
body footer {
  font-size: 5.625rem;
  margin-top: 3.75rem;
  margin-bottom: 0.625rem;
}

:root {
  --dkcyan: hsl(158, 36%, 37%);
  --cream: hsl(30, 38%, 92%);
  --verydkblue: hsl(212, 21%, 14%);
  --dkgrayishblue: hsl(228, 12%, 48%);
  --white: hsl(0, 0%, 100%);
}

h1,
h2,
h3 {
  font-family: "Fraunces", sans-serif;
  margin-top: 0;
  margin-bottom: 0.625rem;
  line-height: 1.1;
}

h2 {
  font-family: "Montserrat", sans-serif;
}

a,
a:visited,
a:hover {
  cursor: pointer;
}

p {
  line-height: 1.5;
  font-size: 0.875rem;
}